.rotate-option input[type=checkbox]{height:0;width:0;visibility:hidden}.rotate-option label{cursor:pointer;width:40px;height:20px;background:#a23535;border-radius:100px;position:relative;top:5px}.rotate-option label:after{content:'';position:absolute;top:2px;left:2px;width:16px;height:16px;background:#fff;border-radius:90px;transition:0.3s}.rotate-option input:checked+label{background:#bada55}.rotate-option input:checked+label:after{left:calc(100% - 2px);transform:translateX(-100%)}
